.consumer {
  background: #F5F5F5;
}
.consumer .search-bar {
  background: #FAFAFA;
}
.consumer .search-bar .am-search-input {
  background: #EDEDED;
}
.consumer-list {
  padding: 0 0.18rem;
  box-sizing: border-box;
  border-top: 0.1rem solid #F5F5F5;
}
.consumer-list .time {
  font-size: 0.12rem;
  font-weight: 400;
  color: #666666;
  line-height: 0.19rem;
  margin: 0.07rem 0 0.11rem;
}
.consumer-list .time span {
  float: right;
  font-size: 0.12rem;
  font-weight: 400;
  color: #dba131;
}
.consumer-list .con {
  border-bottom: 0.01rem solid #F5F5F5;
}
.consumer-list .con h4 {
  font-size: 0.15rem;
  font-weight: 500;
  color: #333333;
  line-height: 0.21rem;
  margin: 0.13rem 0 0.1rem;
  position: relative;
}
.consumer-list .con h4::before {
  content: '';
  width: 0.02rem;
  height: 0.14rem;
  background: #E9B551;
  left: -0.1rem;
  top: 0.03rem;
  display: inline-block;
  position: absolute;
}
.consumer-list .con p {
  font-size: 0.13rem;
  font-weight: 400;
  color: #999999;
  line-height: 0.19rem;
  margin-bottom: 0.09rem;
}
.consumer-list .tips {
  padding: 0.05rem 0.19rem;
}
.consumer-list .tips span {
  float: left;
  font-size: 0.14rem;
  margin: 0.05rem 0.1rem;
  margin-left: 0;
  width: 0.7rem;
  height: 0.3rem;
  line-height: 0.32rem;
  border-radius: 0.06rem;
  border: 0.01rem solid #E9B551;
  text-align: center;
  color: #E9B551;
}
.consumer-list .tips span:last-child {
  margin-right: 0;
}
.consumer-item {
  width: 3.75rem;
}
.consumer-item .header {
  border-bottom: 0.01rem solid #F5F5F5;
  padding: 0.12rem 0;
  box-sizing: border-box;
}
.consumer-item .header > p:first-child {
  line-height: 0.19rem;
  color: #333333;
  font-size: 0.15rem;
  margin-bottom: 0.1rem;
}
.consumer-item .header > p:last-child {
  line-height: 0.19rem;
  color: #dba131;
  font-size: 0.15rem;
}
.consumer-item .detail {
  padding: 0.12rem 0.19rem;
  box-sizing: border-box;
  border-bottom: 0.1rem solid #F5F5F5;
}
.consumer-item .detail .am-list-item {
  padding-left: 0;
  min-height: auto;
  color: #333333;
  font-size: 0.15rem;
}
.consumer-item .detail .am-list-item .am-list-line .am-list-content {
  padding: 0;
  line-height: normal;
  color: #333333;
  font-size: 0.15rem;
}
.consumer-item .detail .am-list-item .am-checkbox-inner {
  border-radius: 0;
}
.consumer-item .detail .am-flexbox {
  color: #333333;
  font-size: 0.15rem;
  margin: 0.12rem 0;
  line-height: 0.2rem;
  height: 0.25rem;
}
.consumer-item .detail .am-flexbox .am-flexbox-item:first-child {
  text-align: left;
}
.consumer-item .detail .am-flexbox .am-flexbox-item:last-child {
  text-align: right;
}
.consumer-item .detail .am-flexbox .am-flexbox-item:last-child.confirming {
  color: #DBA131;
}
.consumer-item .detail .am-flexbox .am-flexbox-item:last-child.apply-success {
  color: #4E93E3;
}
.consumer-item .detail .am-flexbox .am-flexbox-item:last-child.cancel {
  color: #ADADAD;
}
.consumer-item .detail .am-flexbox .am-flexbox-item:last-child.sign-success {
  color: #43C38E;
}
.consumer-item .detail .card {
  line-height: 0.19rem;
  color: #999999;
  font-size: 0.15rem;
}
.give-up {
  width: 100%;
  height: 0.4rem;
  background: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  padding-bottom: 0.6rem;
}
.give-up .give-btn {
  width: 3.43rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  line-height: 0.4rem;
  text-align: center;
  color: #ffffff;
  font-size: 16px;
  margin: 0 auto;
}
.give-up p {
  color: #999999;
  font-size: 0.12rem;
  text-align: center;
  padding-top: 0.1rem;
}
.evaluate .evaluate-header {
  width: 100%;
  height: 2rem;
  background: url(../../static/media/evaluate.5251923a.png) no-repeat left top;
  background-size: 100%;
}
.evaluate .evaluate-header .title {
  width: 2rem;
  line-height: 0.26rem;
  color: #f5a623;
  font-size: 0.16rem;
  text-align: center;
  padding-top: 0.22rem;
  margin: 0 auto;
}
.evaluate .evaluate-tips {
  width: 3.2rem;
  height: 1.1rem;
  border-radius: 0.06rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.07rem 0.21rem 0rem rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  margin-top: -1rem;
  margin-bottom: 0.3rem;
  padding: 0.18rem;
  box-sizing: border-box;
}
.evaluate .evaluate-tips p {
  color: #333333;
  font-size: 15px;
  font-family: PingFangSC-Medium;
}
.evaluate .evaluate-tips .title {
  margin-bottom: 0.1rem;
}
.evaluate .star {
  padding: 0 0.28rem;
  height: 0.4rem;
  line-height: 0.4rem;
}
.evaluate .star .name {
  width: 0.8rem;
  margin-right: 0.2rem;
}
.evaluate .star img {
  width: 0.23rem;
  vertical-align: middle;
  margin-right: 0.2rem;
}
.evaluate .star .left input {
  width: 2rem;
  height: 0.32rem;
  border: 0.01rem solid #eeeeee;
  padding-left: 0.08rem;
}
.evaluate .star .left input::-webkit-input-placeholder {
  width: 0.84rem;
  height: 0.26rem;
  font-size: 0.12rem;
  font-weight: 400;
  color: #999999;
  line-height: 0.26rem;
}
.evaluate .star .left input::-moz-placeholder {
  width: 0.84rem;
  height: 0.26rem;
  font-size: 0.12rem;
  font-weight: 400;
  color: #999999;
  line-height: 0.26rem;
}
.evaluate .star .left input:-ms-input-placeholder {
  width: 0.84rem;
  height: 0.26rem;
  font-size: 0.12rem;
  font-weight: 400;
  color: #999999;
  line-height: 0.26rem;
}
.evaluate .star .left input::-ms-input-placeholder {
  width: 0.84rem;
  height: 0.26rem;
  font-size: 0.12rem;
  font-weight: 400;
  color: #999999;
  line-height: 0.26rem;
}
.evaluate .star .left input::placeholder {
  width: 0.84rem;
  height: 0.26rem;
  font-size: 0.12rem;
  font-weight: 400;
  color: #999999;
  line-height: 0.26rem;
}
.evaluate .evaluate-a {
  text-align: center;
}
.evaluate .evaluate-a .evaluate-area {
  width: 3.4rem;
  height: 1rem;
  border-radius: 0.02rem;
  border: 0.01rem solid #eeeeee;
  margin: 0.3rem auto;
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
.evaluate .evaluate-a .evaluate-area::-webkit-input-placeholder {
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
.evaluate .evaluate-a .evaluate-area::-moz-placeholder {
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
.evaluate .evaluate-a .evaluate-area:-ms-input-placeholder {
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
.evaluate .evaluate-a .evaluate-area::-ms-input-placeholder {
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
.evaluate .evaluate-a .evaluate-area::placeholder {
  line-height: 0.4rem;
  text-indent: 0.2rem;
}
.consumer-info .consumer-title {
  width: 3.75rem;
  padding: 0.12rem 0.16rem;
  box-sizing: border-box;
  border-bottom: 1px solid #EEEEEE;
}
.consumer-info .consumer-title h4 {
  line-height: 0.25rem;
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.12rem;
}
.consumer-info .consumer-title p {
  line-height: 0.19rem;
  color: #666666;
  font-size: 0.14rem;
}
.evaluate-btn {
  padding-bottom: 0.4rem;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  z-index: 10;
}
.evaluate-btn .btn {
  width: 3.43rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  line-height: 0.4rem;
  color: #ffffff;
  font-size: 0.16rem;
  margin: 0 auto;
}
.certificate .certificate-con {
  position: relative;
  width: 3.43rem;
  height: 4.44rem;
  padding: 0.4rem 0.16rem;
}
.certificate .certificate-con img {
  width: 100%;
  height: 100%;
}
.certificate .certificate-con p {
  width: 100%;
  text-align: center;
  color: #333333;
  font-size: 0.18rem;
}
.certificate .certificate-con .name {
  padding-top: 1.7rem;
  margin-bottom: 0.05rem;
}
.certificate .certificate-con .text1 {
  font-size: 0.12rem;
}
.certificate .certificate-con .text2 {
  font-size: 0.12rem;
  width: 70%;
  margin: 0 auto;
}
.certificate .certificate-con .text3 {
  font-size: 0.12rem;
}
.certificate .certificate-con span {
  position: absolute;
  left: 0.65rem;
  bottom: 1.45rem;
  font-size: 0.12rem;
  color: #333333;
}
.get-certificate .am-list-item {
  padding-left: 0;
}
.get-certificate .am-textarea-label,
.get-certificate .am-input-label {
  text-align: right;
  color: #000000;
  font-size: 0.15rem;
  width: 0.8rem !important;
  text-align: left;
  padding-left: 0.2rem;
  margin-right: 0;
}
.get-certificate .am-list-content {
  width: 0.8rem !important;
  flex: none !important;
  color: #000000 !important;
  font-size: 0.15rem !important;
  padding-left: 0.2rem;
}
.get-certificate .am-list-item .am-list-line .am-list-extra {
  color: #999999;
  font-size: 0.15rem;
  flex-basis: 70%;
  text-align: left;
}
.get-certificate .am-textarea-item {
  border-top: 1px solid #EEEEEE;
}
.get-certificate input::-webkit-input-placeholder,
.get-certificate textarea::-webkit-input-placeholder {
  color: #999999;
  font-size: 0.15rem;
}
.get-certificate input::-moz-placeholder,
.get-certificate textarea::-moz-placeholder {
  color: #999999;
  font-size: 0.15rem;
}
.get-certificate input:-ms-input-placeholder,
.get-certificate textarea:-ms-input-placeholder {
  color: #999999;
  font-size: 0.15rem;
}
.get-certificate input::-ms-input-placeholder,
.get-certificate textarea::-ms-input-placeholder {
  color: #999999;
  font-size: 0.15rem;
}
.get-certificate input::placeholder,
.get-certificate textarea::placeholder {
  color: #999999;
  font-size: 0.15rem;
}
